/* Styles pour le panneau de contrôle des notifications */

.notification-panel {
  background: white;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  padding: 24px;
  margin: 20px 0;
  max-width: 800px;
}

.notification-panel h3 {
  margin: 0 0 24px 0;
  color: #333;
  font-size: 20px;
  font-weight: 600;
}

.panel-section {
  margin-bottom: 32px;
  padding-bottom: 24px;
  border-bottom: 1px solid #eee;
}

.panel-section:last-child {
  border-bottom: none;
  margin-bottom: 0;
  padding-bottom: 0;
}

.panel-section h4 {
  margin: 0 0 16px 0;
  color: #555;
  font-size: 16px;
  font-weight: 500;
}

/* Indicateur de statut */
.status-indicator {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 16px;
}

.status-dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  display: inline-block;
}

.status-dot.active {
  background-color: #28a745;
  box-shadow: 0 0 0 2px rgba(40, 167, 69, 0.3);
}

.status-dot.inactive {
  background-color: #dc3545;
  box-shadow: 0 0 0 2px rgba(220, 53, 69, 0.3);
}

.status-text {
  font-weight: 500;
  color: #333;
}

.last-check {
  color: #666;
  font-size: 14px;
  font-style: italic;
}

/* Boutons de contrôle */
.control-buttons {
  display: flex;
  gap: 12px;
  flex-wrap: wrap;
}

.btn {
  padding: 8px 16px;
  border: none;
  border-radius: 4px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
  display: inline-flex;
  align-items: center;
  gap: 4px;
}

.btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.btn-success {
  background-color: #28a745;
  color: white;
}

.btn-success:hover:not(:disabled) {
  background-color: #218838;
}

.btn-danger {
  background-color: #dc3545;
  color: white;
}

.btn-danger:hover:not(:disabled) {
  background-color: #c82333;
}

.btn-primary {
  background-color: #007bff;
  color: white;
}

.btn-primary:hover:not(:disabled) {
  background-color: #0056b3;
}

.btn-warning {
  background-color: #ffc107;
  color: #212529;
}

.btn-warning:hover:not(:disabled) {
  background-color: #e0a800;
}

/* Section de test */
.test-info {
  margin: 8px 0 0 0;
  color: #666;
  font-size: 14px;
  font-style: italic;
}

/* Grille de statistiques */
.stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 16px;
}

.stat-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px;
  background-color: #f8f9fa;
  border-radius: 4px;
  border: 1px solid #e9ecef;
}

.stat-label {
  color: #666;
  font-size: 14px;
}

.stat-value {
  font-weight: bold;
  color: #333;
  font-size: 16px;
}

/* Types de notifications */
.notification-types {
  list-style: none;
  padding: 0;
  margin: 0;
}

.notification-types li {
  padding: 8px 0;
  border-bottom: 1px solid #f0f0f0;
  color: #333;
}

.notification-types li:last-child {
  border-bottom: none;
}

/* Options de configuration */
.config-options {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.config-options label {
  display: flex;
  align-items: center;
  gap: 8px;
  cursor: pointer;
  color: #333;
}

.config-options input[type="checkbox"] {
  width: 16px;
  height: 16px;
  cursor: pointer;
}

/* Responsive */
@media (max-width: 768px) {
  .notification-panel {
    margin: 10px 0;
    padding: 16px;
  }
  
  .control-buttons {
    flex-direction: column;
  }
  
  .btn {
    width: 100%;
    justify-content: center;
  }
  
  .stats-grid {
    grid-template-columns: 1fr;
  }
}
