/* Styles pour le dropdown de notifications */

.notification-dropdown {
  position: absolute;
  top: 100%;
  right: 0;
  width: 380px;
  max-width: 90vw;
  background: white;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.15);
  z-index: 1050;
  animation: slideDown 0.2s ease-out;
  max-height: 500px;
  display: flex;
  flex-direction: column;
}

@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Header du dropdown */
.notification-dropdown-header {
  padding: 16px 20px 12px;
  border-bottom: 1px solid #f0f0f0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #f8f9fa;
  border-radius: 8px 8px 0 0;
}

.notification-dropdown-header h4 {
  margin: 0;
  font-size: 16px;
  font-weight: 600;
  color: #333;
  display: flex;
  align-items: center;
  gap: 8px;
}

.unread-badge {
  background: #dc3545;
  color: white;
  font-size: 12px;
  font-weight: bold;
  padding: 2px 6px;
  border-radius: 10px;
  min-width: 18px;
  text-align: center;
}

.btn-mark-all-read {
  background: none;
  border: none;
  color: #007bff;
  font-size: 12px;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 4px;
  padding: 4px 8px;
  border-radius: 4px;
  transition: all 0.2s ease;
}

.btn-mark-all-read:hover {
  background: #e3f2fd;
  color: #0056b3;
}

/* Body du dropdown */
.notification-dropdown-body {
  flex: 1;
  overflow-y: auto;
  max-height: 400px;
}

.no-notifications {
  padding: 40px 20px;
  text-align: center;
  color: #666;
}

.no-notifications i {
  font-size: 24px;
  margin-bottom: 8px;
  display: block;
  color: #ccc;
}

.no-notifications p {
  margin: 0;
  font-size: 14px;
}

/* Liste des notifications */
.notification-list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.notification-item {
  display: flex;
  align-items: flex-start;
  padding: 12px 20px;
  border-bottom: 1px solid #f0f0f0;
  cursor: pointer;
  transition: background-color 0.2s ease;
  position: relative;
  gap: 12px;
}

.notification-item:hover {
  background-color: #f8f9fa;
}

.notification-item:last-child {
  border-bottom: none;
}

.notification-item.unread {
  background-color: #f0f8ff;
}

.notification-item.unread:hover {
  background-color: #e6f3ff;
}

/* Icône de notification */
.notification-icon {
  font-size: 20px;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #f8f9fa;
  border-radius: 50%;
  flex-shrink: 0;
}

/* Contenu de la notification */
.notification-content {
  flex: 1;
  min-width: 0;
}

.notification-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 4px;
  gap: 8px;
}

.notification-type {
  font-size: 11px;
  font-weight: bold;
  color: #666;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.notification-date {
  font-size: 11px;
  color: #999;
  flex-shrink: 0;
}

.notification-message {
  font-size: 13px;
  color: #333;
  line-height: 1.4;
  margin-bottom: 2px;
}

.notification-client {
  font-size: 12px;
  color: #666;
  font-style: italic;
}

/* Indicateur non lu */
.unread-indicator {
  position: absolute;
  left: 8px;
  top: 50%;
  transform: translateY(-50%);
  width: 6px;
  height: 6px;
  background: #007bff;
  border-radius: 50%;
}

/* Footer du dropdown */
.notification-dropdown-footer {
  padding: 12px 20px;
  border-top: 1px solid #f0f0f0;
  background: #f8f9fa;
  border-radius: 0 0 8px 8px;
}

.btn-view-all {
  width: 100%;
  background: none;
  border: 1px solid #ddd;
  color: #666;
  padding: 8px 12px;
  border-radius: 4px;
  font-size: 13px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 6px;
  transition: all 0.2s ease;
}

.btn-view-all:hover {
  background: #e9ecef;
  border-color: #ccc;
  color: #333;
}

/* Responsive */
@media (max-width: 768px) {
  .notification-dropdown {
    width: 320px;
    right: -50px;
  }
  
  .notification-item {
    padding: 10px 16px;
  }
  
  .notification-dropdown-header,
  .notification-dropdown-footer {
    padding: 12px 16px;
  }
  
  .notification-message {
    font-size: 12px;
  }
}

/* Styles pour l'intégration avec le theme existant */
.notification-dropdown .fas {
  color: inherit;
}

/* Barre de défilement personnalisée */
.notification-dropdown-body {
  scrollbar-width: thin;
  scrollbar-color: #ccc #f0f0f0;
}

.notification-dropdown-body::-webkit-scrollbar {
  width: 6px;
}

.notification-dropdown-body::-webkit-scrollbar-track {
  background: #f0f0f0;
}

.notification-dropdown-body::-webkit-scrollbar-thumb {
  background: #ccc;
  border-radius: 3px;
}

.notification-dropdown-body::-webkit-scrollbar-thumb:hover {
  background: #999;
}
